import { useState } from 'react';
import logo from './logo.svg';
import './App.css';
import Modal from 'react-modal';

// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAnalytics } from "firebase/analytics";
import { getFirestore, doc, setDoc, updateDoc, arrayUnion } from "firebase/firestore";


const quran = require('./quran.json');

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};



// TODO: Add SDKs for Firebase products that you want to use
// https://firebase.google.com/docs/web/setup#available-libraries

// Your web app's Firebase configuration
// For Firebase JS SDK v7.20.0 and later, measurementId is optional
const firebaseConfig = {
  apiKey: "AIzaSyCpR4HVIWlBKL9Ull8ROcerbXgp_RKTD6U",
  authDomain: "quran-tafakkur.firebaseapp.com",
  projectId: "quran-tafakkur",
  storageBucket: "quran-tafakkur.appspot.com",
  messagingSenderId: "271308492930",
  appId: "1:271308492930:web:ab5ef2e0e46b32cec6e49b",
  measurementId: "G-1SYGFV9GYL"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
const analytics = getAnalytics(app);

const db = getFirestore(app);


function App() {

  const [sura, setSura] = useState(' ');
  const [aya, setAya] = useState();
  const [thought, setThought] = useState("");
  const [modalIsOpen, setIsOpen] = useState(false);
  const [saving, setSaving] = useState(false);

    Modal.setAppElement("#root");

  function openModal(aya) {
    setAya(aya);
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }
  function afterOpenModal() {
    // references are now sync'd and can be accessed.
  }

  async function saveThought() {
    const ayaRef = doc(db, "ayat", sura);
    if (thought.length < 3) return;
    try {
      setSaving(true);
      await updateDoc(ayaRef, {[aya[1]]: arrayUnion(thought)});
    } catch (e) {
      setSaving(true);
      await setDoc(ayaRef, {[aya[1]]: [thought]})
    } finally {
      setSaving(false);
    }
  }

  var ayat = '';
  if (sura) {
    ayat = quran[sura].map(aya => <p key={aya[0]} onClick={() => openModal(aya)}>{aya[1]}</p>)
  }
  return (
    <div className="App">
      <header className="App-header">
        <h1> </h1>
        <select onChange={e => setSura(e.target.value)}>
          {Object.keys(quran).map(value => {
            return <option key={value} value={value}>{value}</option>
          })}
        </select>
        {ayat}
        
        <Modal
          isOpen={modalIsOpen}
          onAfterOpen={afterOpenModal}
          onRequestClose={closeModal}
          style={customStyles}
          contentLabel=""
        >
          <div className="modal-content">
            <textarea onChange={e => setThought(e.target.value)} rows="4" cols="50" style={{textAlign: "right"}}></textarea>
            <br />
            <div style={{display: "flex", flexDirection: "row", justifyContent: "space-between", width: "100px", direction: "rtl"}}>
              <button disabled={saving ? "disabled": null} onClick={saveThought}>{saving ? '...  ' : ''}</button>
              <button onClick={closeModal}></button>
            </div>
          </div>
        </Modal>
      </header>
    </div>
  );
}

export default App;
